const sm = " md:justify-between md:flex-row "
const md = " justify-around flex-col "



function Slogan_Style() {
	return {
		container: 
      " w-screen h-screen flex justify-around items-center ",



		containerSection: 
      " w-5/6 h-3/5 flex items-center " +
        sm + 
        md ,
	}
}

export default Slogan_Style;